<template>
    <yd-layout class="chooseLeader">
        <div class="mainContentFluid">
            <div class="approval">
                选择审批人
            </div>
            <div class="fluidWidth">
                <div class="tripAppro">
                    <div class="name_circle">
                        <ul>
                            <li v-for="(item, index) in approver" :key="index">
                                <div class="lastname" v-bind:class="{'check':leaderNum == index}" v-on:click="leaderNum=index">
                                    {{item.empname.substring(0,1)}}
                                    <div class="nameClick">
                                    </div>
                                    <div class="imgClick">
                                    </div>
                                </div>
                                <div class="name">
                                    {{item.empname}}
                                </div>
                            </li>
                        </ul>
                        <div class="clear"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="reason_sec" v-if="$route.params.step!='4'">
            <div class="reasonTitle">填写附言</div>
            <trsTextarea placeholder="附言最多为100汉字..." v-model="remarks"></trsTextarea>
        </div>
        <yd-tabbar slot="tabbar">
            <div class="sendLeader" @click="sendLeaderBtn()">送领导审批</div>
            <div class="submit" @click="$router.go(-1)">返回</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    data() {
        return {
            leaderNum: 99,
            approver: [],
            leaderIDs: '',
            remarks: '',
            activityID: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('公章申请');
        vm.approver = vm.getSealInfo.approver;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getSealInfo'])
    },
    methods: {
        ...mapMutations([
            'setSealInfo'
        ]),
        sendLeaderBtn() {
            let vm = this;
            if (vm.leaderNum == 99) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                if (vm.checkNum != 99) {
                    vm.leaderIDs = vm.approver[vm.leaderNum].operatorid;
                }
                if (vm.$route.params.step == '2') {
                    vm.activityID = 'officialSeal_submitApplication';
                } else if (vm.$route.params.step == '4') {
                    vm.activityID = 'officialSeal_acceptApplication';
                }
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'officialSeal',
                    'approveType': vm.getSealInfo.approveType,
                    'workItemID': vm.$route.params.workId,
                    'activityID': vm.activityID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': vm.remarks,
                    'opinion': '',
                    'workTitle': vm.getSealInfo.jsonData.firstorgname + vm.getSealInfo.jsonData.applicantname + '公章使用申请',
                    'ext': '{"applyid":"' + vm.getUserInfo.userid + '","applyname":"' + vm.getSealInfo.jsonData.applicantname + '","applyoffsid":"' + vm.getSealInfo.applyoffsid + '","applyoffsname":"' + vm.getSealInfo.applyoffsname + '","applyreason":"' + vm.getSealInfo.jsonData.reason + '","applyremark":"' + vm.transferString(vm.getSealInfo.jsonData.remarks) + '"}'
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.setSealInfo({
                            applyoffsname: '',
                            approver: [],
                            approveType: '',
                            applyoffsid: '',
                            jsonData: '',
                            fuyan: '',
                            sealArray: [],
                            fileNum: 0,
                            lineNum: -1
                        })
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle/');
                            }
                        });
                    }
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "./commonSeal.less";

.submit {
    background-color: #a4ceff;
}

.approval {
    height: .8rem !important;
    line-height: .8rem !important;
}

.name_circle ul li {
    margin-right: .03rem;
    margin-bottom: .2rem;
}
</style>


